// ========================================================
// 数値から単位を取り除く(フォントサイズ・幅・高さの計算などに使用。超便利)
// =========================================================
@function strip-unit($number) {
  @if type-of($number) == "number" and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}

// ========================================================
// px→remの計算
// 考え方
// px/ブラウザ標準のpx(初期値16px)×1rem
// =========================================================
@function rem($px, $base: 16px) {
  $value: $px;
  @if (unit($px) != "px") { // 単位がpx以外の場合は警告を出してそのまま返す
    @warn 'rem()の引数にpx以外の値を指定しても計算できません';
    @return $value;
  }
  $value: (strip-unit($px) / strip-unit($base)) * 1rem;
  @return $value;
}

// ========================================================
// px→vwの計算
// =========================================================
@function vw($px, $base: 375px) {
  $value: $px;
  @if (unit($px) != "px") { // 単位がpx以外の場合は警告を出してそのまま返す
    @warn 'vw()の引数にpx以外の値を指定しても計算できません';
    @return $value;
  }
  $value: (strip-unit($px) / strip-unit($base)) * 100vw;
  @return $value;
}

// ========================================================
// px→%の計算
// =========================================================
@function per($px, $base: 375px) {
  $value: $px;
  @if (unit($px) != "px") { // 単位がpx以外の場合は警告を出してそのまま返す
    @warn 'per()の引数にpx以外の値を指定しても計算できません';
    @return $value;
  }
  $value: (strip-unit($px) / strip-unit($base)) * 100%;
  @return $value;
}

// ========================================================
// font-size
// =========================================================
@function fs($pc: 16px, $sp: 14px, $base: $container-size){
  @if(unit($pc) != "px" or unit($sp) != "px"){
    @warn 'fontsize()の引数にpx以外の値を指定しても計算できません';
    @return $pc;
  }
  @return clamp($sp, vw($pc, $base), $pc);
}

// ========================================================
// line-height
// =========================================================
@function lh($font-size, $line){
  $value: (strip-unit($line) / strip-unit($font-size));
  @return $value;
}
