.l-index-mv{
  width: 100%;
  height: calc(100vh - 134px);
  min-height: 714px;
  background-image: url(../img/index/kv-bg.jpg);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  @include mq(md){
    background-image: url(../img/index/kv-bg@sp.jpg);
    height: 100vh;
    background-position: left -190px top -50px;
    background-size: auto calc(100% + 51px);
  }
}
.p-index-mv{
  &__lead{
    color: #FFF;
    position: absolute;
    top: 209px;
    left: per(128px, $inner-width);
    @include mq(md){
      top: 176px;
      left: per(22px, $inner-width-sp);
      line-height: lh(50, 100);
      letter-spacing: 0.02em;
    }
    span{
      font-family: $font-theme;
      font-weight: $fw-medium;
      font-size: fs(92px, 50px, $inner-width);
      line-height: lh(92, 120);
      letter-spacing: 0.03em;
      display: inline-block;
    }
  }
  &__entry{
    position: absolute;
    bottom: 90px;
    left: 0;
    background: linear-gradient(90deg, #037EF2 0%, #62B7BF calc(100% + 115px));
    @include mq(md){
      bottom: 80px;
    }
    & > a{
      @extend .c-link__arrow--white;
      width: vw(712px, $inner-width);
      height: 120px;
      padding-right: 45px;
      color: #FFF;
      font-size: 1.8rem;
      font-weight: $fw-medium;
      line-height: lh(18, 24);
      letter-spacing: 0.1em;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      @include mq(md){
        width: vw(339px, $inner-width-sp);
        height: 72px;
        font-size: 1.4rem;
        line-height: lh(14, 11);
      }
      &::after{
        @include mq(md){
          margin-left: 19.2px;
          display: block;
          width: 38px;
          height: 38px;
          border-radius: 19px;
        }
      }
    }
  }
  &__sns{
    position: absolute;
    bottom: 131px;
    right: 128px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 26px;
    @include mq(md){
      display: none;
    & > a{
      transition: transform .3s;
      &:hover{
        transform: scale(1.1);
      }
    }
    }
    & > a{
      transition: transform .3s;
      &:hover{
        transform: scale(1.2);
      }
    }
  }
}

.l-index-scrollbar{
  position: relative;
  width: 100%;
  &::after{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    content: "";
    display: block;
    width: 2px;
    height: 160px;
    background: linear-gradient(0deg, rgba(#FFF, 0) 0%, rgba(#FFF, 0) 33%, #037EF2 33%, #62B7BF 66%, rgba(#FFF, 0) 66%, rgba(#FFF, 0) 100%);
    background-size: 100% 300%;
    background-repeat: no-repeat;
    animation: scrollbarAnim 1.7s ease-in-out infinite;
    @keyframes scrollbarAnim{
      0%{
        background-position: center top 101%;
        opacity: 1;
      }
      50%{
        background-position: center top 51%;
        opacity: 0.9;
      }
      100%{
        opacity: 0;
        background-position: center top 0%;
      }
    }
    @include mq(md){
      height: 120px;
    }
  }
}

