.l-index-works{
  background: linear-gradient(180deg, #037EF2 0%, #62B7BF 100%);
}
.p-index-works{
  &__inner{
    padding: 110px 0 120px clamp(0px, #{vw(128px, $inner-width)}, 128px);
    @include mq(md){
      padding: 80px 0 80px 22px;
    }
  }
  &__title{
    color: #FFF;
    margin-bottom: 28px;
    @include mq(md){
      margin-bottom: 10px;
    }
    &__main{
      font-family: $font-theme;
      font-size: 9rem;
      line-height: 1;
      letter-spacing: 0.08em;
      margin-top: 10px;
      @include mq(md){
        font-size: 5.2rem;
      }
    }
    &__sub{
      font-size: 1.6rem;
      line-height: 1;
      @include mq(md){
        font-size: 1.4rem;
      }
    }
  }
  &__lead{
    color: #FFF;
    font-size: 1.8rem;
    font-weight: $fw-light;
    line-height: lh(18, 32);
    letter-spacing: 0.05em;
    @include mq(md){
      font-size: 1.4rem;
      line-height: lh(14, 24);
    }
  }
  &__slide{
    &__list{
      &__item{
        display: flex;
        flex-flow: column;
        justify-content: flex-start;
        align-items: center;
        transition: opacity .3s;
        &__image{
          width: 100%;
          height: 272px;
          flex-shrink: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          overflow: hidden;
          @include mq(md){
            width: auto;
            height: auto;
            flex-shrink: unset;
            overflow: visible;
          }
          &>img{
            width: auto;
            max-width: unset;
            height: 100%;
            @include mq(md){
              width: unset;
              max-width: 100%;
              height: unset;
            }
          }
        }
        &__detail{
          display: flex;
          flex-flow: column;
          justify-content: flex-start;
          align-items: flex-start;
          color: #FFF;
          margin-top: 32px;
          height: 100%;
          @include mq(md){
            margin-top: 12px;
          }
        }
        &__title{
          font-size: 2.2rem;
          font-weight: $fw-medium;
          line-height: lh(22, 34);
          min-height: calc(2em * #{lh(22, 34)});
          @include mq(md){
            font-size: 1.6rem;
            line-height: lh(16, 28);
          }
        }
        &__description{
          margin-top: 12px;
          font-size: 1.6rem;
          line-height: lh(16, 28);
          @include mq(md){
            font-size: 1.4rem;
            line-height: lh(14, 26);
          }
        }
        &__tag{
          color: #EAF8FA;
          font-size: 1.4rem;
          line-height: lh(14, 40);
          margin-top: auto;
          @include mq(md){
            margin-top: 16px;
          }
        }
        &:nth-child(1){ .u-anim--fadeleft{ transition-delay: .1s } }
        &:nth-child(2){ .u-anim--fadeleft{ transition-delay: .2s } }
        &:nth-child(3){ .u-anim--fadeleft{ transition-delay: .3s } }
        &:nth-child(4){ .u-anim--fadeleft{ transition-delay: .4s } }
        &:nth-child(5){ .u-anim--fadeleft{ transition-delay: .5s } }
        &:nth-child(6){ .u-anim--fadeleft{ transition-delay: .6s } }
        &:nth-child(7){ .u-anim--fadeleft{ transition-delay: .7s } }
        &:nth-child(8){ .u-anim--fadeleft{ transition-delay: .8s } }
