.l-index-committees{
  background: linear-gradient(180deg, #037EF2 0%, #62B7BF 100%);
  padding: 120px 22px;
}
.p-index-committees{
  &__inner{
    max-width: 100%;
    width: 1184px;
    margin: 0 auto;
  }
  &__title{
    color: #FFF;
    text-align: center;
    &__sub{
      font-size: 1.4rem;
      line-height: lh(14, 24);
      letter-spacing: 0.08em;
    }
    &__main{
      font-family: $font-theme;
      font-size: 4.2rem;
      line-height: lh(42, 56);
      letter-spacing: 0.02em;
      margin-top: 10px;
    }
    &__lead{
      font-size: 1.4rem;
      font-weight: $fw-light;
      line-height: lh(14, 24);
      letter-spacing: 0.08em;
      margin-top: 26px;
    }
  }
  &__anchor{
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: clamp(0px, #{vw(52px, $inner-width)}, 52px);
    &__item{
      color: #FFF;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover{
        color: #FFF;
      }
      &::after{
        content: "";
        width: 12px;
        height: 8px;
        display: block;
        background-image: url(../img/common/tab-arrow.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        margin-left: 6px;
        margin-top: 3px;
        transform: translateY(0);
        transition: transform .3s;
      }
      &:hover::after{
        transform: translateY(3px);
      }
    }
  }
  &__content{
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: wrap;
    gap: 50px 16px;
    margin-top: 60px;
    &__area{
      width: calc((100% - 16px) / 2);
      color: #FFF;
      @include mq(md){
        width: 100%;
      }
    }
    &__head{
      width: 100%;
      height: 80px;
      border: solid 1px #FFF;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &__inner{
      display: flex;
      justify-content: flex-start;
      align-items: stretch;
      flex-wrap: wrap;
      gap: 40px 16px;
      margin-top: 40px;
    }
    &__item{
      font-size: 1.6rem;
      font-weight: $fw-light;
      line-height: lh(16, 24);
      width: calc((100% - 16px) / 2);
      display: flex;
      justify-content: flex-start;
      align-content: center;
      // text-decoration: underline;
      @include mq(md){
        font-size: 1.2rem;
      }
    }
  }
}

