.l-header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background: linear-gradient(180deg, #FFF 0%, #FFF 50%, rgba(#FFF, 0) 50%, rgba(#FFF, 0) 100%);
  background-size: 100% 200%;
  background-position: left bottom;
  box-shadow: 0 0 0 rgba(#000, 0);
  transition: background-position .5s, box-shadow .5s .5s;
  &.is-fixed{
    background-position: left top;
    box-shadow: 0 10px 10px rgba(#000, 0.05);
  }
  &__inner{
    max-width: 100%;
    width: $inner-width;
    height: 100px;
    margin: 0 auto;
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
    color: #FFF;
    @include mq(tb){
      height: 82px;
    }
    .is-fixed &{
      color: #202222;
    }
  }
}
.p-header{
  &__logo{
    flex-shrink: 0;
    font-size: 0;
    width: 154px;
    height: 100%;
    display: block;
    margin-left: 20px;
    background-image: url(../img/common/header-logo.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 9999;
    @include mq(tb){
      width: 122px;
    }
    .is-fixed &{
      background-image: url(../img/common/header-logo@fixed.svg);
    }
    &.is-white{
      background-image: url(../img/common/header-logo.svg);
    }
  }
  &__content{
    margin-left: per(111px, $inner-width);
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    @include mq(tb){
      display: flex;
      z-index: -1;
      opacity: 0;
      visibility: hidden;
      transition: opacity .5s, visibility .5s, z-index .5s;
      position: fixed;
      margin: 0;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#000, 0.95);
      flex-flow: column;
      justify-content: center;
      align-items: center;
      font-size: 1.8rem;
      &.is-show{
        opacity: 1;
        visibility: visible;
        z-index: 9998;
        overflow: scroll;
        color: #FFF;
      }
    }
  }
  &__nav{
    @include mq(tb){
      order: 1;
    }
    &__inner{
      display: flex;
      justify-content: center;
      align-items: center;
      gap: vw(40px, $inner-width);
      @include mq(tb){
        flex-flow: column;
        gap: 30px;
      }
    }
    &__item{
      font-size: 1.6rem;
      font-family: $font-theme;
      font-weight: $fw-medium;
      letter-spacing: 0.1em;
      @include mq(tb){
        font-size: 1.8rem;
      }
    }
  }
  &__lang{
    margin-left: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    @include mq(tb){
      margin: 30px auto 0;
      order: 4;
    }
    &__item{
      font-size: 1.4rem;
      font-family: $font-theme;
      font-weight: $fw-medium;
      opacity: 0.6;
      letter-spacing: 0.1em;
      @include mq(tb){
        font-size: 1.8rem;
      }
      &.is-active{
        opacity: 1;
        pointer-events: none;
      }
    }
    &__separator{
      width: 1px;
      height: 12px;
      background-color: #FFF;
      margin: 0 vw(16px, $inner-width);
      @include mq(tb){
        margin: 0 16px;
      }
      .is-fixed &{
        background-color: #202222;
      }
    }
  }
  &__contact{
    margin-left: per(80px, $inner-width);
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    max-width: 212px;
    width: vw(212px, $inner-width);
    flex-shrink: 0;
    background-color: #202222;
    align-self: stretch;
    font-size: fs(18px, 14px, $inner-width);
    letter-spacing: 0.1em;
    color: #FFF;
    @include mq(tb){
      order: 2;
      margin: 30px auto 0;
      background-color: unset;
      max-width: unset;
      width: unset;
    }
  }
  &__sns{
    display: none;
    @include mq(tb){
      order: 3;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 26px;
      margin-top: 60px;
    }
  }
  &__hamburger{
    cursor: pointer;
    width: 72px;
    height: 100%;
    display: block;
    position: relative;
    z-index: 9999;
    margin-left: auto;
    display: none;
    @include mq(tb){ display: block }

    $_width: 30px;
    $_height: 2px;
    $_gap: 5px;
    i, i::before, i::after{
      width: $_width;
      height: $_height;
      background-color: #FFF;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      transition: transform .5s, margin .5s;
      .is-fixed &{
        background-color: #202222;
      }
    }
    i::before, i::after{ content: ""; }
    i::before{ margin-top: calc((#{$_gap} + #{$_height}) * -1); }
    i::after{  margin-top: calc((#{$_gap} + #{$_height})); }
    .is-show + & > i{
      background-color: rgba(#FFF, 0);
      &::before{
        margin-top: 0;
        transform: translateX(-50%) translateY(-50%) rotate(225deg);
        background-color: #FFF;
      }
      &::after{
        margin-top: 0;
        transform: translateX(-50%) translateY(-50%) rotate(-225deg);
        background-color: #FFF;
      }
    }
  }
}