.l-footer{
  background-color: #EAF8FA;
  @include mq(md){
    background-color: #FFF;
  }
  &__inner{
    max-width: 100%;
    width: calc(#{$inner-width} + 44px);
    height: 174px;
    margin: 0 auto;
    padding: 0 22px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    @include mq(md){
      padding: 0;
      height: auto;
      align-items: flex-start;
      flex-flow: column;
    }
  }
}

.p-footer{
  &__logo{
    flex-shrink: 0;
    font-size: 0;
    width: 317px;
    height: 100%;
    display: block;
    margin-left: 20px;
    background-image: url(../img/common/footer-logo.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    @include mq(md){
      order: 2;
      width: 213px;
      height: 44px;
      margin: 24px 22px 0;
    }
  }
  &__copylight{
    color: #707070;
    font-size: 1.2rem;
    font-weight: $fw-medium;
    line-height: lh(12, 18);
    margin-left: clamp(0px, #{vw(45px, $inner-width)}, 45px);
    @include mq(md){
      order: 3;
      margin: 22px 22px 40px;
    }
  }
  &__sns{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 42px;
    margin-left: auto;
    @include mq(md){
      padding: 32px 22px;
      order: 1;
      justify-content: center;
      margin-left: unset;
      background-color: #EAF8FA;
      width: 100%;
    }
    & > a{
      transition: transform .3s;
      &:hover{
        transform: scale(1.2);
      }
    }
  }
}