html, body {
  width: 100%;
  min-height: 100%;

  @include mq(pc) {
    overflow:auto !important;
  }
}

html {
  box-sizing: border-box;
  -ms-overflow-style: scrollbar;
  font-size: 62.5%;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  -webkit-text-size-adjust: 100%;
  color: $text;
  font-size: 1.6rem;
  font-family: $font-noto;
  margin: 0;
  overflow-x: hidden;
  position: relative;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  transition: all .3s ease;

  img {
    backface-visibility: hidden;
  	-webkit-backface-visibility: hidden;
    transition: .3s ease;
  }

  &:hover {
    @include mq(hv) {
      text-decoration: none;

      img {
        opacity: .6;
      }
    }
  }

  &:hover {
    opacity: .6;

    @include mq(hv) {
      // color: $over;
    }
  }
}

a[href^="tel:"] {
  @include mq(pc) {
    pointer-events: none;
  }
}

img {
  max-width: 100%;
  height: auto;
}

p {
  line-height: 1.8;

  + p {
    margin-top: 2em;
  }

  small {
    font-size: 1.2rem;
  }

  a {
    color: $link;
    text-decoration: underline;

    &:hover {
      @include mq(hv) {
        color: $over;
        text-decoration: none;
      }
    }
  }
}

h1, h2, h3, h4, h5, h6, p {
  a {
  }
}

strong {
  font-weight: bold;
}

td {
  a {
    text-decoration: underline;
    @include mq(hv) {
      &:hover {
        text-decoration: none;
      }
    }
  }
}

hr {
  height: 1px;
  background-color: $grayL;
  border: none;
  margin: 20px 0;
}


.disnone {
  display: none;
}

.list-inline>li {
  display: inline-block;
}
