.c-link{
  &__arrow--white{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    &::after{
      content: "";
      display: inline-block;
      margin-left: 31.5px;
      width: 36px;
      height: 8px;
      background-image: url(../img/common/arrow-white.svg);
      background-position: left center;
      background-repeat: no-repeat;
      background-size: contain;
      transform: translateX(0);
      transition: transform .3s;
    }
    &:hover{
      color: #FFF;
    }
    &:hover::after{
      transform: translateX(10px);
    }
  }
  &__arrow--black{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    &::after{
      content: "";
      display: inline-block;
      margin-left: 12px;
      width: 20px;
      height: 4px;
      background-image: url(../img/common/arrow-black.svg);
      background-position: left center;
      background-repeat: no-repeat;
      background-size: contain;
      transform: translateX(0);
      transition: transform .3s;
    }
    &:hover{
      color: inherit;
    }
    &:hover::after{
      transform: translateX(10px);
    }
  }
  &__sns{
    font-size: 0;
    width: 38px;
    height: 38px;
    display: block;
    background-position: center;
    background-repeat: no-repeat;
    &--note{
      background-image: url(../img/common/icon-note.svg);
      background-size: 25.37px 22.54px;
    }
    &--twitter{
      background-image: url(../img/common/icon-twitter.svg);
      background-size: 22px 27.05px;
    }
    &--instagram{
      background-image: url(../img/common/icon-instagram.svg);
      background-size: 38px 38px;
    }
  }
}