@charset "UTF-8";

$breakpoints: (
  "sm": screen and (max-width: 360px),
  "pr": screen and (max-width: 540px),
  "md": screen and (max-width: 768px),
  "tb": screen and (max-width: 1024px),
  "lg": screen and (max-width: 1280px),

  "hv": screen and (min-width: 769px),
  "pc": screen and (min-width: 1025px),
  "bs": screen and (min-width: 1281px),
) !default;

// ========================================================
// ブレイクポイント
// =========================================================
@mixin mq($breakpoint) {

  // マップ内に指定の値があるかを探し、真偽値を返す
  @if map-has-key($breakpoints, $breakpoint) {
    @media #{inspect(map-get($breakpoints, $breakpoint))}  {
      @content;
    }
  }

  // マップ型で定義されていない値が呼び出された時はエラーを返す
  @else {
    @error "指定されたブレークポイントは定義されていません。"+"指定できるブレークポイントは次のとおりです。 -> #{map-keys($breakpoints)}";
  }
}
// ========================================================
// /ブレイクポイント
// =========================================================


// ========================================================
// z-index
// 書き方
// .hoge {
//   z-index: map-get($layer, "header");
// }
// 共通定義したドロワー内はz-index: map-get($layer, "drawer") + 1;
// あるいはz-index: map-get($layer, "drawer") - 1;など
// =========================================================
$layer: (
  modal: 100,
  drawer: 40,
  sp-menu: 35,
  floating: 30,
  header: 20,
  default: 1,
);
// ========================================================
// /z-index
// =========================================================


// ========================================================
// 縦横比を維持したままレスポンシブ対応
// 書き方
// .hoge {
//    @include aspect-ratio(16, 9);
//「paddingの上下%指定は親要素の横幅が基準となる」という仕様を利用してpaddingで高さを作る
// =========================================================
@mixin aspect-ratio($width, $height) {
  position: relative;

  &::before {
    content: '';
    float: left;
    padding-top: ($height / $width) * 100%;
  }

  &::after {
    content: '';
    display: block;
    clear: both;
  }

  & > :first-child {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }
}
// ========================================================
// /縦横比を維持したままレスポンシブ対応
// =========================================================


// ========================================================
// 最前列・最後列のマージン指定
// =========================================================
@mixin edge-margin($m:0){
  >*:first-child{
    margin-top: $m;
  }
  >*:last-child{
    margin-bottom: $m;
  }
}
// ========================================================
// /最前列・最後列のマージン指定
// =========================================================
