html[lang="en"]{
  .p-header__contact{
    font-family: $font-theme;
    font-size: fs(16px, 12px, $inner-width);
    padding: 0 16px;
    line-height: 1.4;
    width: vw(222px, $inner-width);
  }
  .p-index-mv__entry{
    font-family: $font-theme;
  }
  .p-index-lead__main{
    font-family: $font-theme;
  }
  .p-index-data__item__detail__main{
    font-family: $font-theme;
  }
  .p-index-data__item{
    &__unit{
      font-family: $font-theme;
    }
    &__main{
      justify-content: center;
    }
    &__plus{
      align-self: center;
      font-size: fs(92px, 0px, $inner-width);
    }
    &__num--small + &__plus{
      margin-bottom: 20px;
    }
    &__detail{
      margin-top: 30px;
      &__unit{
        color: #037EF2;
        font-size: 1.8rem;
        font-weight: $fw-medium;
        letter-spacing: 0.08em;
      }
      &__main{
        margin-top: 20px;
      }
    }
  }
  .p-index-vision__description{
    font-family: $font-theme;
  }
  .p-index-mission__description{
    font-family: $font-theme;
  }
  .p-index-works__lead{
    font-family: $font-theme;
    letter-spacing: 0.08em;
  }
  .p-index-briefing__content__entry{
    font-family: $font-theme;
  }
}