body{
  opacity: 0;
  &.is-loaded{
    transition: opacity .2s ease-in;
    opacity: 1;
  }
}

.u-anim{
  &--indexmv{
    &__lead{
      &--pc{
        background: linear-gradient(90deg, #037EF2 0%, #62B7BF 100%);
        background-size: 200% 100%;
        background-position: left 200% center;
        background-repeat: no-repeat;
        color: rgba(#FFF, 0);
        .is-loaded .is-active &{
          transition: background-position .6s .3s ease-in-out, color .5s 1.1s;
          background-position: left -101% center;
          color: rgba(#FFF, 1);
        }
        &:nth-of-type(n + 2){
          margin-top: 16px;
        }
        @include mq(md){
          background: none;
        }
      }
      &--sp{
        background: none;
        @include mq(md){
          background: linear-gradient(90deg, #037EF2 0%, #62B7BF 100%);
          background-size: 200% 100%;
          background-position: left 200% center;
          background-repeat: no-repeat;
          color: rgba(#FFF, 0);
          .is-loaded .is-active &{
            transition: background-position .6s .3s ease-in-out, color .5s 1.1s;
            background-position: left -101% center;
            color: rgba(#FFF, 1);
          }
          &:nth-of-type(n + 2){
            margin-top: 16px;
          }
        }
      }
    }
    &__entry{
      left: vw(-712px, $inner-width);
      @include mq(md){
        left: vw(-339px, $inner-width-sp);
      }
      & > a{
        opacity: 0;
      }
      .is-loaded .is-active &{
        transition: left .6s 1.3s ease-in-out;
        left: 0;
        & > a{
          transition: opacity .5s 1.9s;
          opacity: 1;
        }
      }
    }
  }
  &--slidein{
    overflow: hidden;
    transform: translateX(-100%);
    opacity: 0;
    transition: transform .8s ease-in-out, opacity .7s;
    .is-loaded &.is-active{
      transform: translateX(0%);
      opacity: 1;
    }
    &__inner{
      width: 100%;
      display: inline-block;
      transform: translateX(100%);
      opacity: 0;
      transition: transform .8s ease-in-out, opacity .7s;
      .is-loaded .is-active > &{
        transform: translateX(0%);
        opacity: 1;
      }
    }
    &--right{
      overflow: hidden;
      transform: translateX(100%);
      opacity: 0;
      transition: transform .8s ease-in-out, opacity .7s;
      .is-loaded &.is-active{
        transform: translateX(0%);
        opacity: 1;
      }
      &__inner{
        width: 100%;
        display: inline-block;
        transform: translateX(-100%);
        opacity: 0;
        transition: transform .8s ease-in-out, opacity .7s;
        .is-loaded .is-active > &{
          transform: translateX(0%);
          opacity: 1;
        }
      }
    }
    &--delay, &--delay > &__inner, &--delay > &--right__inner{
      transition-delay: .8s !important;
    }
    &--fast--delay, &--fast--delay > &__inner, &--fast--delay > &--right__inner{
      transition-delay: .4s !important;
    }
    &--fast, &--fast > &__inner, &--fast > &--right__inner{
      transition-duration: .6s, .525s;
      transition-property: transform, opacity;
    }
  }
  &--fadeup{
    transform: translateY(100px);
    opacity: 0;
    transition: transform .7s ease-in-out, opacity .7s;
    .is-loaded &.is-active{
      transform: translateY(0);
      opacity: 1;
    }
  }
  &--fadeleft{
    transform: translateX(50px);
    opacity: 0;
    transition: transform .7s ease-in-out, opacity .7s;
    .is-loaded &.is-active{
      transform: translateX(0);
      opacity: 1;
    }
  }
  &--bg-slide-left{
    &.is-active{
    }
  }
  &--briefing{
    &__entry{
      width: 0;
      transition: width .6s ease-in-out;
      & > a{
        transition: opacity .5s .7s;
        opacity: 0;
      }
      .is-loaded &.is-active{
        width: 100%;
        @include mq(md){
          width: vw(339px, $inner-width-sp);
        }
        & > a{
          opacity: 1;
        }
      }
    }
  }
}
