.c-slide{
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    height: auto;
  }
  &__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 66px;
    padding-right: clamp(0px, #{vw(128px, $inner-width)}, 128px);
    @include mq(md){
      justify-content: flex-start;
      align-items: flex-start;
      flex-flow: column;
      margin-bottom: 40px;
      padding-right: 22px;
    }
  }
  &__control{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    @include mq(md){
      margin-top: 24px;
      margin-left: auto;
    }
    &--prev, &--next{
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 36px;
      height: 36px;
      cursor: pointer;
      border-radius: 50%;
      flex-shrink: 0;
    }
    &--prev{
      background-image: url(../img/common/slide-arrow-left.svg);
      margin-right: clamp(0px, vw(32px, $inner-width), 32px);
      transform: translateX(0);
      transition: transform .3s;
      &:hover{
        transform: translateX(-10px);
      }
      @include mq(md){
        margin-right: 32px;
      }
    }
    &--next{
      background-image: url(../img/common/slide-arrow-right.svg);
      transform: translateX(0);
      transition: transform .3s;
      &:hover{
        transform: translateX(10px);
      }
    }
  }
}